export const dataService = {
    getColors,
    getCars
}

function getColors(){
    return colors.map(item => { return { value: item.name, label: item.name } })    
}

function getCars(Make){
  var newCars = []
  for (var i=0;i<cars.length;i++){
      var found = false
      for (var j=i+1;j<cars.length;j++) {
          if (cars[i].Make === cars[j].Make && cars[i].Model === cars[j].Model) {
              var found = true;
              break;
          }
      }
      if (!found) {
          var make = cars[i].Make;
          var model = cars[i].Model;
          newCars.push({make,model});
      }
  }

  newCars = newCars.filter(car=>{
      return car.make === Make
  })

  var mapCars = newCars.map(item => { return { value: item.model, label: item.model } })    
  return mapCars
}

